<template>
  <div class="home">
    <img class="background-image hidden-xs-only" src="@/../static/homeBig.jpg">
    <img class="background-image hidden-sm-and-up" src="@/../static/homeSmall.jpg">
    <div class="title">{{ slogan }}</div>
    <floating-button
      class="blog-button hidden-xs-only"
      :content="blog"
      routeName="Blog"
      left="35%"
      top="20%"
      width="10em"
      height="10em"
      fontSize="1.5em">
    </floating-button>
    <floating-button
      class="blog-button hidden-sm-and-up"
      :content="blog"
      routeName="Blog"
      left="40%"
      top="20%"
      width="7em"
      height="7em"
      fontSize="1.2em">
    </floating-button>
    <floating-button
      class="about-button hidden-xs-only"
      :content="about"
      routeName="About"
      left="7%"
      top="58%"
      width="7em"
      height="7em"
      fontSize="1.2em">
    </floating-button>
    <floating-button
      class="about-button hidden-sm-and-up"
      :content="about"
      routeName="About"
      left="15%"
      top="63%">
    </floating-button>
    <floating-button
      class="login-button hidden-xs-only"
      :content="login"
      routeName="Login"
      left="80%"
      top="5%">
    </floating-button>
  </div>
</template>

<script>
import constants from '@/utils/constants';
import FloatingButton from './components/FloatingButton';

export default {
  name: 'Home',
  components: {
    FloatingButton,
  },
  data() {
    return {
      blog: '博客',
      about: '关于作者',
      login: '作者登录',
      slogan: '',
      bigBackPicUrl: '',
      smallBackPicUrl: '',

      // 传进FloatingButton的参数
      content: 'lala',
      color: 'red',
    };
  },
  mounted() {
    // 获取大背景图
    this.$http.get(`${constants.serverUrl}/home/bigBackPic`)
      .then((response) => {
        this.bigBackPicUrl = `@/../${response.body.bigBackPicUrl}`;
      });
    // 获取小背景图
    this.$http.get(`${constants.serverUrl}/home/smallBackPic`)
      .then((response) => {
        this.smallBackPicUrl = `@/../${response.body.smallBackPicUrl}`;
      });
    // 获取主页标语
    this.$http.get(`${constants.serverUrl}/home/slogan`)
      .then((response) => {
        this.slogan = response.body.slogan;
      });
  },
  methods: {
  },
};
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
.home {
  width: 100vw;
  height: 100vh;
}

.background-image {
  position: absolute;
  width: 100vw;
  height: 100vh;
  z-index: -1;
}

.title {
  position: absolute;
  left: 2%;
  top: 2%;
  font-weight: bold;
  font-size: 2.5em;
  color: #fb3;
}

.blog-button {
  animation: aniOne 10s infinite;
}

.about-button {
  animation: aniTwo 10s infinite;
}

.login-button {
  animation: aniThree 10s infinite;
}

@keyframes aniOne {
  0% {transform: translate(0, 0);}
  25% {transform: translate(1.5em, .5em);}
  50% {transform: translate(.5em, -.5em);}
  75% {transform: translate(1.5em, .5em)}
  100% {transform: translate(0, 0);}
}

@keyframes aniTwo {
  0% {transform: translate(0, 0);}
  25% {transform: translate(1em, .5em);}
  50% {transform: translate(.5em, 0);}
  75% {transform: translate(1em, -.5em)}
  100% {transform: translate(0, 0);}
}

@keyframes aniThree {
  0% {transform: translate(0, 0);}
  25% {transform: translate(-.5em, 1em);}
  50% {transform: translate(.5em, 0);}
  75% {transform: translate(-.5em, .5em)}
  100% {transform: translate(0, 0);}
}
</style>



